import React, { useState, useEffect } from "react";
import jsonBrand from "./brand";
// import { useFetchAllBrands } from "../BrandContext/BrandContext";

const AllBrands = React.createContext([]);

const AllBrandsProvider = ({ children }) => {
    // const brands = useFetchAllBrands("//corp-platform-context.k8s-qa-cvc.com.br/api/Context");
    const brands = jsonBrand;
    return <AllBrands.Provider value={brands}>{children}</AllBrands.Provider>;
};

const fetchbrand = async () => {
    return new Promise(resolve => {
        setTimeout(() => {
            resolve(jsonBrand);
        }, 300);
    });
};

const useFetchAllBrands = endpoint => {
    const [brands, setBrands] = useState(null);
    useEffect(() => {
        (async function () {
            fetchbrand().then((d) => setBrands(d));
        })();
    }, []);
    return brands;
};

export {
    AllBrandsProvider,
    AllBrands,
    fetchbrand,
    useFetchAllBrands,
};

